import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
    components: {
        uniLoadMore
    },
    data() {
        return {
            headerPosition:"fixed",
            headerTop:"0px",
            loadingType: 'more', //加载更多状态
            filterIndex: 0,
            priceOrder: 0, //1 价格从低到高 2价格从高到低
            goodsList: [],
            startNum:0
        };
    },

    onLoad(options){



        this.loadData();
    },
    onPageScroll(e){
        //兼容iOS端下拉时顶部漂移
        if(e.scrollTop>=0){
            this.headerPosition = "fixed";
        }else{
            this.headerPosition = "absolute";
        }
    },
    //下拉刷新
    onPullDownRefresh(){
        this.loadData('refresh');
    },
    //加载更多
    onReachBottom(){
        console.log("onReachBottom")
        this.startNum = this.startNum+8
        this.loadData();
    },
    methods: {
        //加载商品 ，带下拉刷新和上滑加载
         loadData(type='add', loading) {
             console.log("onloadStart---------------------------------------------")
            //没有更多直接返回
                if(this.loadingType === 'nomore'){
                 console.log("所有数据查询查询完成，不再进行查询操作")
                    return;
                }

             var params = this.startNum
             this.$http.post("selectFruitInfoBatch?startNum="+params).then((res) => {
                 console.log("success")
                 console.log(res)
                 if (res.data.status == "success") {
                     if(res.data.data.length > 0){
                         let goodList = res.data.data;
                         this.goodsList = this.goodsList.concat(goodList);
                     }else{
                         console.log("已查询完所有数据")
                         this.$api.msg("没有更多了！")
                         this.loadingType  = 'nomore'
                     }

                 }else {
                     this.$api.msg("error");
                 }
             }).catch(error => {
                 console.log(error)
             })

            //判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
            // this.loadingType  = this.goodsList.length > 20 ? 'nomore' : 'more';
            if(type === 'refresh'){
                if(loading == 1){
                    uni.hideLoading()
                }else{
                    uni.stopPullDownRefresh();
                }
            }

            console.log("onloadEnd**************************************************")
        },
        //筛选点击
        tabClick(index){
            if(this.filterIndex === index && index !== 2){
                return;
            }
            this.filterIndex = index;
            if(index === 2){
                this.priceOrder = this.priceOrder === 1 ? 2: 1;
            }else{
                this.priceOrder = 0;
            }
            uni.pageScrollTo({
                duration: 300,
                scrollTop: 0
            })
            // uni.showLoading({
            //     title: '正在加载'
            // })
            if(this.filterIndex === 1){
                for(var item in this.goodsList){
                    console.log(this.goodsList[item].sales)
                }
                this.goodsList.sort((a,b)=>b.sales - a.sales)
                for(var item in this.goodsList){
                    console.log(this.goodsList[item].sales)
                }
            }
            if(this.filterIndex === 2){
                for(var item in this.goodsList){
                    console.log(this.goodsList[item].price)
                }
                this.goodsList.sort((a,b)=>{
                    if(this.priceOrder == 1){
                        return a.price - b.price;
                    }
                    return b.price - a.price;
                })
                for(var item in this.goodsList){
                    console.log(this.goodsList[item].price)
                }
            }

        },
        //详情
        navToDetailPage(item){
            //测试数据没有写id，用title代替
            let id = item.fruitId;
            uni.navigateTo({
                url: `/pages/product/product?id=`+id
            })
        },
        stopPrevent(){}
    },
}
